                        <label className="sr-only">Subscribe</label>
                        <input
                          type="text"
                          id="hero-input"
                          name="hero-input"
                          className="py-3 px-4 block w-full border-transparent shadow-sm rounded-md focus:z-10 focus:border-blue-500 focus:ring-blue-500"
