import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';

import DashboardLayout from './dashboard/layout';
import Dashboard from './pages/admin/Dashboard';
import Inbox from './pages/admin/Inbox';
import Contact from './pages/admin/Contact';
import SocialMedia from './pages/admin/SocialMedia';
import LiveFeeds from './pages/admin/LiveFeeds';
// import Login from './Login';
import Register from './Register';
import { useState } from 'react';
import { authContext } from './Context/AuthContext';
import Login from './Login';
import FbTest from './pages/admin/FbTest';
import Home from './pages/Home';
import IgTest from './pages/admin/IgTest';
import TelTest from './pages/admin/TelTest';

function App() {
  const [logged, setLogged] = useState(false);
  const [userName, setUserName] = useState(undefined);
  const [userEmail, setUserEmail] = useState(undefined);
  const [userRole, setUserRole] = useState(undefined);

  return (
    <authContext.Provider
      value={{
        logged,
        setLogged,
        userName,
        setUserName,
        userEmail,
        setUserEmail,
        userRole,
        setUserRole,
      }}
    >
      <Router>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/signIn" element={<Login />} />
          <Route path="/signUp" element={<Register />} />
        </Routes>
        <DashboardLayout>
          <Routes>
            <Route path="/dashboard" element={<Dashboard />} />
            <Route path="/inbox" element={<Inbox />} />
            <Route path="/contacts" element={<Contact />} />
            <Route path="/socialMedia" element={<SocialMedia />} />
            <Route path="/liveFeeds" element={<LiveFeeds />} />
            <Route path="/fbTest" element={<FbTest />} />
            <Route path="/igTest" element={<IgTest />} />
            <Route path="/telTest" element={<TelTest />} />
          </Routes>
        </DashboardLayout>
      </Router>
    </authContext.Provider>
  );
}

export default App;
