import Axios from 'axios';
import React, { useContext, useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';
import { authContext } from './Context/AuthContext';

function Register() {
  const [email, setEmail] = useState('');
  const [username, setUsername] = useState('');
  const [role, setRole] = useState('');
  const [password, setPassword] = useState('');
  const [cpassword, setCPassword] = useState('');

  const { setLogged } = useContext(authContext);
  const { setUserName } = useContext(authContext);
  const { setUserEmail } = useContext(authContext);
  const { setUserRole } = useContext(authContext);

  const navigate = useNavigate();

  const registerUser = (e) => {
    e.preventDefault();

    if (email && username && role && password && cpassword) {
      Axios.post(
        'https://us-east4-shroom-72ad0.cloudfunctions.net/create_acccount',
        {
          message: {
            username: username,
            email: email,
            password: password,
            cpassword: password,
            role: role,
            account_type: '',
          },
        },
      )
        .then((res) => {
          console.log('Posting Data From::', res);
          // if (res.data.code == 102) {
          //   localStorage.setItem('userEmail', email);
          //   localStorage.setItem('account_type', role);
          //   setLogged(true);
          //   setUserName(username);
          //   setUserEmail(email);
          //   setUserRole(role);
          //   Swal.fire({
          //     title: 'Registered Successfully!',
          //     text: 'Welcome to Shroom',
          //     icon: 'success',
          //     confirmButtonText: 'Cool',
          //   });
          //   navigate('/dashboard');
          // }
        })
        .catch((err) => console.log(err.message));
    } else {
      Swal.fire({
        title: 'Error!',
        text: 'All Fields Are Required',
        icon: 'error',
        confirmButtonText: 'Cool',
      });
    }
  };

  useEffect(() => {
    checkLogin();
  }, []);

  const checkLogin = () => {
    if (localStorage.getItem('userEmail')) {
      const useRole = localStorage.getItem('account_type');
      if (useRole === 'admin') {
        setLogged(true);
        navigate('/dashboard');
      }
    }
  };

  return (
    <>
      <div>
        <div className="bg-blue-400 absolute top-0 left-0 bg-gradient-to-b from-gray-900 via-gray-900 to-blue-400 bottom-0 leading-5 h-full w-full overflow-hidden"></div>
        <div className="relative   min-h-screen  sm:flex sm:flex-row  justify-center bg-transparent rounded-3xl shadow-xl">
          <div className="flex-col flex  self-center lg:px-14 sm:max-w-4xl xl:max-w-md  z-10">
            <div className="self-start hidden lg:flex flex-col  text-gray-300">
              <h1 className="my-3 font-semibold text-2xl">
                Welcome to Mi-Ujamo
              </h1>
              <img
                alt="login"
                src="/images/login.png"
                className="mx-auto object-cover rounded-full  "
              />
            </div>
          </div>
          <div className="flex justify-center self-center  z-10">
            <div className="p-12 bg-white mx-auto rounded-3xl w-96 ">
              <div className="mb-7">
                <h3 className="font-semibold text-2xl text-gray-800">
                  Sign Up{' '}
                </h3>
              </div>
              <div className="space-y-6">
                <div className="">
                  <input
                    className=" w-full text-sm  px-4 py-3 bg-gray-200 focus:bg-gray-100 border  border-gray-200 rounded-lg focus:outline-none focus:border-blue-400"
                    type="email"
                    value={email}
                    placeholder="Email"
                    onChange={(e) => {
                      setEmail(e.target.value);
                    }}
                  />
                </div>
                <div className="">
                  <input
                    className=" w-full text-sm  px-4 py-3 bg-gray-200 focus:bg-gray-100 border  border-gray-200 rounded-lg focus:outline-none focus:border-blue-400"
                    type="text"
                    value={username}
                    placeholder="Username"
                    onChange={(e) => {
                      setUsername(e.target.value);
                    }}
                  />
                </div>

                <div className="">
                  <input
                    className=" w-full text-sm  px-4 py-3 bg-gray-200 focus:bg-gray-100 border  border-gray-200 rounded-lg focus:outline-none focus:border-blue-400"
                    type="text"
                    value={role}
                    placeholder="Role"
                    onChange={(e) => {
                      setRole(e.target.value);
                    }}
                  />
                </div>

                <div className="relative">
                  <input
                    type="password"
                    value={password}
                    placeholder="Password"
                    className="text-sm  px-4 py-3 rounded-lg w-full bg-gray-200 focus:bg-gray-100 border border-gray-200 focus:outline-none focus:border-blue-400"
                    onChange={(e) => {
                      setPassword(e.target.value);
                    }}
                  />
                </div>
                <div className="relative">
                  <input
                    type="password"
                    value={cpassword}
                    placeholder="CPassword"
                    className="text-sm  px-4 py-3 rounded-lg w-full bg-gray-200 focus:bg-gray-100 border border-gray-200 focus:outline-none focus:border-blue-400"
                    onChange={(e) => {
                      setCPassword(e.target.value);
                    }}
                  />
                </div>

                <div>
                  <button
                    onClick={registerUser}
                    type="submit"
                    className="w-full flex justify-center bg-blue-500  hover:bg-blue-700 text-gray-100 p-3  rounded-lg tracking-wide font-semibold  cursor-pointer transition ease-in duration-500"
                  >
                    Register
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>

        <svg
          className="absolute bottom-0 left-0 "
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 1440 320"
        >
          <path
            fill="#fff"
            d="M0,0L40,42.7C80,85,160,171,240,197.3C320,224,400,192,480,154.7C560,117,640,75,720,74.7C800,75,880,117,960,154.7C1040,192,1120,224,1200,213.3C1280,203,1360,149,1400,122.7L1440,96L1440,320L1400,320C1360,320,1280,320,1200,320C1120,320,1040,320,960,320C880,320,800,320,720,320C640,320,560,320,480,320C400,320,320,320,240,320C160,320,80,320,40,320L0,320Z"
          ></path>
        </svg>
      </div>
    </>
  );
}

export default Register;
