import Axios from 'axios';
import React, { useContext, useEffect, useState } from 'react';
import Swal from 'sweetalert2';
import { authContext } from './Context/AuthContext';
import { Link, useNavigate } from 'react-router-dom';

function Login() {
  const [loading, setLoading] = useState(false);

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();

  const { setLogged } = useContext(authContext);
  const { setUserName } = useContext(authContext);
  const { setUserEmail } = useContext(authContext);
  const { setUserRole } = useContext(authContext);

  useEffect(() => {
    checkLogin();
  }, []);

  const checkLogin = () => {
    if (localStorage.getItem('userEmail')) {
      navigate('/dashboard');
    }
  };

  const loginUser = (e) => {
    e.preventDefault();

    if (email && password) {
      setLoading(true);
      Axios.post('https://us-central1-shroom-72ad0.cloudfunctions.net/login', {
        message: { email: email, lpassword: password },
      })
        .then((res) => {
          // console.log('Posting Data From::', res.data);
          // localStorage.setItem('userEmail', email);

          if (res.data.code == 404) {
            setLoading(false);
            Swal.fire({
              title: 'Error!',
              text: 'User not found',
              icon: 'error',
              confirmButtonText: 'ok',
            });
          } else if (res.data.code == 505) {
            setLoading(false);
            Swal.fire({
              title: 'Error!',
              text: 'password dont match',
              icon: 'error',
              confirmButtonText: 'ok',
            });
          } else if (res.data.code == 500) {
            Axios.post(
              'https://us-east4-shroom-72ad0.cloudfunctions.net/get_user_info',
              { message: { email: email } },
            )
              .then((res) => {
                // console.log(res.data);
                setLoading(false);
                Swal.fire({
                  title: 'Login Successfuly!',
                  text: 'Welcome to Mxoxa',
                  icon: 'success',
                  confirmButtonText: 'ok',
                });

                localStorage.setItem('userEmail', res.data.user.email);
                localStorage.setItem('userRole', res.data.user.role);
                localStorage.setItem('userName', res.data.user.username);
                setLogged(true);
                setUserName(res.data.user.username);
                setUserEmail(res.data.user.email);
                setUserRole(res.data.user.role);
                navigate('/dashboard');
              })
              .catch((err) => {
                setLoading(false);
                console.log(err.message);
              });
          }
        })
        .catch((err) => {
          setLoading(false);
          console.log(err.message);
        });
    } else {
      Swal.fire({
        title: 'Error!',
        text: 'All Fields Are Required',
        icon: 'error',
        confirmButtonText: 'Cool',
      });
    }
  };

  return (
    <>
      <div
        className={`${
          !loading && 'hidden'
        } w-full h-full fixed  top-0 left-0 bg-white opacity-75 z-50`}
      >
        <span className="text-green-500 opacity-75 top-1/2 my-0 mx-auto block relative w-0 h-0">
          <i className="fas fa-circle-notch fa-spin fa-5x"></i>
        </span>
      </div>

      <div>
        <div className="bg-blue-400 absolute top-0 left-0 bg-gradient-to-b from-gray-900 via-gray-900 to-blue-400 bottom-0 leading-5 h-full w-full overflow-hidden"></div>
        <div className="relative   min-h-screen  sm:flex sm:flex-row  justify-center bg-transparent rounded-3xl shadow-xl">
          <div className="flex-col flex  self-center lg:px-14 sm:max-w-4xl xl:max-w-md  z-10">
            <div className="mx-auto  lg:flex flex-col  text-gray-300">
              <Link to="/">
                <img
                  alt="login"
                  src="/images/fav.png"
                  className="mx-auto object-cover rounded-full w-32 md:w-full "
                />
              </Link>
            </div>
          </div>
          <div className="flex justify-center self-center  z-10">
            <div className="p-12 bg-white mx-auto rounded-3xl w-96 ">
              <div className="mb-7">
                <h3 className="font-semibold text-2xl text-gray-800">
                  Sign In{' '}
                </h3>
              </div>
              <div className="space-y-6">
                <div className="">
                  <input
                    className=" w-full text-sm  px-4 py-3 bg-gray-200 focus:bg-gray-100 border  border-gray-200 rounded-lg focus:outline-none focus:border-blue-400"
                    type="email"
                    placeholder="Email"
                    value={email}
                    onChange={(e) => {
                      setEmail(e.target.value);
                    }}
                  />
                </div>

                <div className="relative">
                  <input
                    type="password"
                    placeholder="Password"
                    className="w-full text-sm  px-4 py-3 bg-gray-200 focus:bg-gray-100 border  border-gray-200 rounded-lg focus:outline-none focus:border-blue-400"
                    value={password}
                    onChange={(e) => {
                      setPassword(e.target.value);
                    }}
                  />
                </div>

                <div>
                  <button
                    type="submit"
                    onClick={loginUser}
                    className="w-full flex justify-center bg-blue-500  hover:bg-blue-700 text-gray-100 p-3  rounded-lg tracking-wide font-semibold  cursor-pointer transition ease-in duration-500"
                  >
                    Sign In
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>

        <svg
          className="absolute bottom-0 left-0 "
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 1440 320"
        >
          <path
            fill="#fff"
            fill-opacity="1"
            d="M0,0L40,42.7C80,85,160,171,240,197.3C320,224,400,192,480,154.7C560,117,640,75,720,74.7C800,75,880,117,960,154.7C1040,192,1120,224,1200,213.3C1280,203,1360,149,1400,122.7L1440,96L1440,320L1400,320C1360,320,1280,320,1200,320C1120,320,1040,320,960,320C880,320,800,320,720,320C640,320,560,320,480,320C400,320,320,320,240,320C160,320,80,320,40,320L0,320Z"
          ></path>
        </svg>
      </div>
    </>
  );
}

export default Login;
